<script>
    import iconEye from "@/assets/icons/eye.svg"
    import iconPencil from "@/assets/icons/pencil.svg"
    import profile from "../modals/profile"
    export default {
        name: "usersTable",
        components: {
            profile
        },
        data: function () {

            return {
                columns: ["id", "Full name", "Country", "Company", "Currency", "Balance", "State", "Preview", "Edit"],
                iconEye,
                iconPencil,
                currency: 'usd',
                selectedUser: {},
                showProfile: false
            }
        },
        props: {
            users: Array
        },
        methods: {},
        filters: {
            formatId(id){ 
                
                let a = id.toString()
                let b = ""
                for(let i=0; i<4-a.length; i++){
                    b+="0"
                }
                let position = 0
                let output = [a.slice(0, position), b, a.slice(position)].join('')
                return output
                
                
            },
            formatDate(value){
                console.log(value)
                if (!value) return ''
                value = value.toString()
                value = value.slice(0, 10).replace(/-/g, ".")
                return value
            },
             upperCase(value){
                if (!value) return ''
                return value.toUpperCase()

            },
            dateFormat(value){
                if (!value) return ''
                value = value.slice(0, 10).replace(/-/g, ".")
                return value
            },
            fixed(value){
                if (!value) return ''
                value = value.toFixed(2)
                return value
            },
            formatAccount(value){
                if (!value) return ''
                value.toString()
                value = "0" + value
                let output = value.substr(0, 3)
                output += " " + value.substr(3, 2)
                output += " " + value.substr(5, 3)
                output += " " + value.substr(8, 4)
                return output
            },
            getCurrencySign(currency){
                const signs = {
                    "eur" : "â¬",
                    "gbp" : "Â£",
                    'usd' : "$",
                    "rub" : "â½"
                    }
                let currencySign = signs[currency] 
                return currencySign
            },
            capitalize(value){
                if (!value) return ''
                value = value.toString()
                return value.charAt(0).toUpperCase() + value.slice(1)
            }
        },
    }
</script>
