<template>
    <div class="wrapper">
        <header>
            <div class="left">
                <img src="../assets/logo_green.png" alt="" width="120" height="120">
                <div class="middle">
                <div class="panel">
                    <button class="add-user" @click="currentTab = 'addUserForm'" :class="{active: currentTab == 'addUserForm'}">Add user</button>
                    <button class="clients" @click="currentTab = 'clients'" :class="{active: currentTab == 'clients'}">Clients</button>
                </div>
            </div>
            </div>
            
            <div class="right">
                <div class="user">
                    <img src="../assets/icons/icon_user_2.svg" alt="" width="45" height="45">
                    <span @click="logOut()">Welcome Mr. Admin 4</span>
                </div>
            </div>
            
        </header>
        <section class="section">
            <component :is="currentTabComponent"  :newUser="newUser" :users="allUsers" @created="msg='User created'" />
            <popUp v-if="msg" :msg="msg" @close="msg=''" />
          
        </section>
        <footer class="footer">
            <div class="copyright"><span>Â© 2020 Copyright. All Rights Reserved. Internet banking software provided by Sterling Payment Services (HK)</span>
            </div>
        </footer>
