<script>
    import addUserForm from '../components/forms/addUserForm.vue'
    import clients from '../components/tables/admin4Clients.vue'
    import popUp from '../components/modals/popUp'
    import {mapGetters, mapActions} from 'vuex'
    export default{
        name: "Admin4",
        components: {
            addUserForm,
            clients,
            popUp
        },
        data: function(){
            return{
                msg: "",
                currentTab: "clients",
                newUser: {}
            }
        },
        methods:{
            logOut(){
                localStorage.clear()
                this.$router.push('/login')
            },
            ...mapActions(['fetchUsers']),
        },
        computed: {
            ...mapGetters(['allUsers', 'getUser', 'allTransactions']),
            currentTabComponent (){
                return this.currentTab
            }
        },
        async mounted(){
           await this.fetchUsers().then(()=>{
                this.loading = false
                if (this.allUsers.length){
                        const last = this.allUsers[this.allUsers.length - 1]
                        this.newUser = {
                        id: last.id + 1 ,
                        accounts: {
                            eur: last.accounts.eur + 1,
                            usd: last.accounts.usd + 1,
                            gbp: last.accounts.gbp + 1,
                            rub: last.accounts.rub + 1
                        }
                     }
                 } else {
                    this.newUser = {
                        id: 1,
                        accounts: {
                            eur: 99790000001,
                            gbp: 99280000001,
                            usd: 99480000001,
                            rub: 99460000001,
                        }
                    }
                }
            }).catch(error => {
                
                this.err = error
                console.log(error)
            })
        }
    }
</script>
